import React, { useState } from 'react';

const Chatbot = () => {
  const [isOpen, setIsOpen] = useState(false); // Trng thi  kim tra chatbot c m hay khng

  const toggleChatbot = () => {
    setIsOpen((prev) => !prev); // o ngc trng thi khi nhn nt
  };

  const styles = {
    button: {
      position: 'fixed', // t nt  v tr c nh
      bottom: '20px', // Khong cch t y trang
      right: '20px', // Khong cch t bn phi
      backgroundColor: '#007bff', // Mu nn
      color: '#fff', // Mu ch
      border: 'none', // Khng c vin
      borderRadius: '20px', // Bo gc cho nt chat
      padding: '10px 15px', // Khong cch bn trong nt
      cursor: 'pointer', // Con tr chut
      boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)', //  bng
      zIndex: 1000, // m bo nt  trn cng
      transition: 'background-color 0.3s', // Hiu ng chuyn mu
    },
    buttonHover: {
      backgroundColor: '#0056b3', // Mu nn khi hover
    },
    container: {
      position: 'fixed', // t iframe  v tr c nh
      bottom: '80px', // Khong cch t y trang
      right: '20px', // Khong cch t bn phi
      zIndex: 1000, // m bo iframe  trn cng
      width: isOpen ? '350px' : '50px', // Chiu rng ty theo trng thi
      height: isOpen ? '430px' : '50px', // Chiu cao ty theo trng thi
      transition: 'width 0.3s ease, height 0.3s ease', // Hiu ng chuyn ng
      overflow: 'hidden', // n phn vt qu
    },
    iframe: {
      width: '100%', // Chiu rng iframe
      height: '100%', // Chiu cao iframe
      border: 'none', // Khng c vin
      borderRadius: '8px', // Bo gc
    },
  };

  return (
    <div>
      {/* Nt chat */}
      <button 
        style={styles.button} 
        onClick={toggleChatbot}
        onMouseEnter={(e) => e.currentTarget.style.backgroundColor = styles.buttonHover.backgroundColor} 
        onMouseLeave={(e) => e.currentTarget.style.backgroundColor = styles.button.backgroundColor}
      >
        {isOpen ? '' : ''} {/* Biu tng chat hoc ng */}
      </button>
      {/* Container cho iframe */}
      <div style={styles.container}>
        {isOpen && (
          <iframe
            title="BetterWE"
            src="https://d42kc5ii20lhsomxz6rgv.ai.copilot.live"
            style={styles.iframe} // S dng styles t bin
            loading="lazy"
            allow="microphone;camera;speaker;clipboard-read;clipboard-write;geolocation;"
          ></iframe>
        )}
      </div>
    </div>
  );
};

export default Chatbot;
